export const validate = () => {
const submitForm = document.getElementById("submitForm");
const firstName = document.getElementById("firstName");
const lastName = document.getElementById("lastName");
const phoneNumber = document.getElementById("phoneNumber");
const servicesList = document.getElementById("servicesList");
const resultForm = document.getElementById("resultForm");
const wrong = "Form is done wrong!!! Please do the following:<br>";
const noValue = "The red rectangle is empty";
const onlyNumbers = "You need to write numbers";
const nineNumbers = "Phone number contain 9 digits. Try again";
const inputs = document.querySelectorAll(".input");
const noValueList = "Choose option from the list. Delete your previous answer";

function wrongAnswer(reason){
    resultForm.classList.add("negative");
    resultForm.innerHTML = wrong+reason;
}

function colorRed(input){
    input.style.borderColor="#A62A2A"; input.style.borderWidth="2px"
}


submitForm.addEventListener("click", function(){
    inputs.forEach((element) => {
        element.style.borderColor= "#333333";
        element.style.borderWidth="1px";
        resultForm.classList.remove("positive");
        resultForm.classList.remove("negative");
         
        });
    
    //For please


    if(firstName.value.length < 1)  { wrongAnswer(noValue); colorRed(firstName);} 
    else if(lastName.value.length < 1)  {wrongAnswer(noValue);colorRed(lastName); }
    else if(phoneNumber.value.length < 1)  {wrongAnswer(noValue);colorRed(phoneNumber); }
    else if ((phoneNumber.value % 1) != 0)  {wrongAnswer(onlyNumbers);colorRed(phoneNumber); }
    else if (phoneNumber.value.length < 9 || phoneNumber.value.length > 9)  {wrongAnswer(nineNumbers);colorRed(phoneNumber); }
    else if(servicesList.value.length < 1)  {wrongAnswer(noValue);colorRed(servicesList); }
    else if(servicesList.value !== "Option1" && servicesList.value !=="Option2" && servicesList.value !=="Option3" && servicesList.value !=="Option4") {wrongAnswer(noValueList);colorRed(servicesList); }
    else {
        resultForm.classList.add("positive");
        resultForm.innerHTML = "Form done correctly";
    }
      

});
}

